import React from "react";
import { Grid } from "@mui/material";
import TurkeyMap from "../components/TurkeyMap";
import Announcements from "../components/announcements";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core";
import Dates2 from "./dates2";
import Dates3 from "./dates3";

const useStyles = makeStyles({
  reklam: {
    fontSize: 45,

    marginLeft: 70,
    marginTop: 10,
    marginBottom: 10
  },
  reklam2: {
    fontSize: 45,
    height: 300,
    width: 300,
    marginLeft: 70,
    marginTop: 10,
    marginBottom: 10
  }
});

export default function Main() {
  const classes = useStyles();
  return (
    <>
      <Grid container>
       
        <Grid
          container
          direction="row"
          justifyContent="center"
          alignItems="center"
          xs={12}
          sm={12}
          md={12}
          style={{backgroundColor:'#272727'}}
        >
          <Grid
            container
            direction="row"
            justifyContent="flex-end"
            alignItems="center"
            xs={6}
            sm={6}
            md={6}
          >
            <Dates2 />
          </Grid>
          <Grid
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center"
            xs={6}
            sm={6}
            md={6}
          >
            &nbsp;&nbsp;&nbsp;
            <Dates3 />
          </Grid>
        </Grid>

        <Grid item xs={12} sm={12} md={9}>
          <br />
          <br />
          <br />
          <TurkeyMap />
        </Grid>
        <Grid item xs={12} sm={12} md={3}>
          <br />
          <br />
          <Announcements />
        </Grid>
        <Grid></Grid>
      </Grid>
      <br />
      <br />
      <br />

      <Grid>
        <Grid>
          <Button className={classes.reklam2} variant="contained">
            REKLAM
          </Button>
          <Button className={classes.reklam} variant="contained">
            REKLAM
          </Button>
          <Button className={classes.reklam} variant="contained">
            REKLAM
          </Button>
        </Grid>
      </Grid>
      <br />
      <br />
      <br />
    </>
  );
}
