import "./App.css";
import { BrowserRouter, Routes, Route } from "react-router-dom";

import FullSizeLayout from "./layouts/fullsizelayout";
import Main from "./pages/main";
import Provinces from "./pages/provinces";
import DetailSearch from "./pages/detailsearch";
import PersonalInfo from "./pages/personalinfo";
import ImamLogin from "./pages/imamlogin";

import useMediaQuery from "@mui/material/useMediaQuery";
import { createTheme, ThemeProvider } from "@mui/material/styles";

import React from "react";
import MerhumInfo from "./pages/merhuminfo";

function App() {
  const prefersDarkMode = useMediaQuery("(prefers-color-scheme: dark)");

  const theme = React.useMemo(
    () =>
      createTheme({
        palette: {
          mode: prefersDarkMode ? "dark" : "light"
        }
      }),
    [prefersDarkMode]
  );

  return (
    <ThemeProvider theme={theme}>
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<FullSizeLayout />}>
            <Route index element={<Main />} />
            <Route path="provinces" element={<Provinces />} />
            <Route path="detailsearch" element={<DetailSearch />} />
            <Route path="personalinfo" element={<PersonalInfo />} />
            <Route path="imamlogin" element={<ImamLogin />} />
            <Route path="merhuminfo" element={<MerhumInfo />} />
          </Route>
        </Routes>
      </BrowserRouter>
    </ThemeProvider>
  );
}

export default App;
