import React from "react";

export default function Footer() {
  return (
    <div>
      <img
        src={require("../assets/images/download-app-store.png")}
        width={160}
        height={50}
      ></img>
      <img
        src={require("../assets/images/download-google-play.png")}
        width={160}
        height={50}
      ></img>
    </div>
  );
}
