import React, { useState, useEffect, useRef } from "react";
import "./turkeymap.css";

import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core";
import Grid from "@mui/material/Grid";
import DenseTable from "./DenseTable";

import TextField from "@mui/material/TextField";

import Dates from "./Dates";

import Card from "@mui/material/Card";
import Typography from "@material-ui/core/Typography";
import Unye from "./Unye";

const useStyles = makeStyles({
  reklam: {
    fontSize: 45,

    marginTop: 10,
    marginBottom: 10
  }
});

export default function Ankara() {
  const [harita, setHarita] = useState(false);
  const [isim, setIsim] = useState("ORDU");

  const classes = useStyles();
  const ref = useRef();
  console.log(ref);
  function RunRef() {
    setHarita(true);
    setIsim("ÜNYE")
  }

  useEffect(() => {
    function svgturkiyeharitasi() {
      const element = document.querySelector("#svg-turkiye-haritasi");
      const info = document.querySelector(".il-isimleri");

      element.addEventListener("mouseover", function (event) {
        if (
          event.target.tagName === "path" &&
          event.target.parentNode.id !== "guney-kibris"
        ) {
          info.innerHTML = [
            "<div>",
            event.target.parentNode.getAttribute("data-iladi"),
            "</div>"
          ].join("");
        }
      });

      element.addEventListener("mousemove", function (event) {
        info.style.top = event.pageY + 25 + "px";
        info.style.left = event.pageX + "px";
      });

      element.addEventListener("mouseout", function (event) {
        info.innerHTML = "";
      });

      element.addEventListener("click", function (event) {
        if (event.target.tagName === "path") {
          const parent = event.target.parentNode;
          const id = parent.getAttribute("id");

          if (id === "guney-kibris") {
            return;
          }

          window.location.href =
            "#" + id + "-" + parent.getAttribute("data-plakakodu");
        }
      });
    }
    svgturkiyeharitasi();
  }, []);

  return (
    <>
      <Grid
        style={{ height: window.screen.height - 400 }}
        container
        xs={12}
        md={12}
        lg={12}
        spacing={2}
      >
        <Grid xs={6} md={6} lg={6}>
          <Unye RunRef={RunRef} />
        </Grid>
        <Grid xs={12} md={6} lg={6}>
          <br /> <br />
          <Button className={classes.reklam} variant="contained">
            REKLAM
          </Button>{" "}
          &nbsp;&nbsp;&nbsp;
          <Button className={classes.reklam} variant="contained">
            REKLAM
          </Button>
          <br />
          <Button className={classes.reklam} variant="contained">
            REKLAM
          </Button>{" "}
          &nbsp;&nbsp;&nbsp;
          <Button className={classes.reklam} variant="contained">
            REKLAM
          </Button>
          <Typography>{isim} VEFAT SAYISI : 21</Typography>
        </Grid>
      </Grid>
      <Grid container>
        <Grid item xs={0} md={4} lg={3}>
          <Card
            style={{
              height: 500,
              width: 250,
              marginLeft: 20,
              backgroundColor: "gray"
            }}
            justifyContent="center"
            alignItems="center"
          >
            <Typography style={{ marginBottom: 30, marginLeft: 15 }}>
              FİLTRELEME SEÇENEKLERİ
            </Typography>

            <Dates />

            <TextField
              margin="dense"
              size="small"
              id="filled-search"
              type="search"
              style={{ width: 400 }}
              label="AD"
            />
            <TextField
              margin="dense"
              size="small"
              id="filled-search"
              type="search"
              style={{ width: 400 }}
              label="SOYAD"
            />
            <TextField
              margin="dense"
              size="small"
              id="filled-search"
              type="search"
              style={{ width: 400 }}
              label="MEMLEKET"
            />

            <Button style={{ marginTop: 15 }} fullWidth variant="contained">
              ARA
            </Button>
          </Card>
        </Grid>

        <Grid item xs={12} md={7} lg={8}>
          {harita && <DenseTable ref={ref} />}
        </Grid>
        <Grid item xs={12} md={1} lg={1}></Grid>
      </Grid>
    </>
  );
}
